body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.MoneyApp {
  margin-top: 20px;
  margin-bottom: 20px;
}

.MoneyApp .card-block {
  padding: 6px;
}

.MoneyApp .card-block a.btn[role="button"] {
  margin: 10px;
  white-space: normal;
}

/* Source: https://stackoverflow.com/a/16447619/1360295 */
.MoneyApp code {
  white-space: pre;
  word-break: normal;
  word-wrap: normal;
}

.appstore {
	display: inline-block;
	overflow: hidden;
	background-size: contain;
}

.appstore-ios, .appstore-ios:active {
	/* width: 135px; */
	height: 40px;
  margin: 10px;
}

.appstore-android img, .appstore-android:active img {
  height: 60px;
  /* 10px margin is built-in to the Google Play badge image as a transparent border */
}

